import React from "react";
import PropTypes from "prop-types";
import {
  Typography,
  makeStyles,
  Box,
  CircularProgress,
} from "@material-ui/core";
import { useBlocksForNode } from '../hooks/useBlocksForNode';

function Blocks({ nodeUrl }) {
  const classes = useStyles();
  const [blocks, isLoading, error] = useBlocksForNode(nodeUrl);

  if (isLoading) {
    return <CircularProgress />;
  }

  if (error) {
    return <Typography color="error" variant="body1">Something went wrong fetching blocks</Typography>;
  }

  if (blocks.length == 0) {
    return <Typography color="error" variant="body1">Empty blocks for given node url</Typography>;
  }

  return (
    <React.Fragment>
      {
        blocks.map(({ id, attributes }) => (
          <Box key={id} className={classes.boxContent}>
            <Typography className={classes.identifier} color="primary">{`${id}`.padStart(3, '0')}</Typography>
            <Typography variant="body2">{attributes.data}</Typography>
          </Box>
        ))
      }
    </React.Fragment>
  );
}

const useStyles = makeStyles((theme) => ({
  identifier: {
    fontSize: theme.typography.pxToRem(11),
    fontWeight: '700',
  },
  boxContent: {
    backgroundColor: "lightGray",
    borderRadius: 4,
    padding: 8,
    marginBottom: 10,
  },
}));

Blocks.propTypes = {
  nodeUrl: PropTypes.string.isRequired,
};

export default Blocks;
